<template>
<main>
    <ChangeModal v-if="modalVisible4" @close4="modalVisible4 = false" :listdata="modalData4"/>
    <Header :header="content"/>
    <input type="text" placeholder="نام کاربر یا بارکد مدنظر را جستجو نمایید ..." v-model="filter" style="width:500px;display:block;margin:10px auto;font-family:yekan;"/>
    <div class="container list">
        <table class="table table-hover container" >
            <thead>
                <tr>
                <th scope="col">ردیف</th>
                <th scope="col">نام</th>
                <th scope="col">نام خانوادگی</th>
                <th scope="col">کد پرسنلی</th>
                <th scope="col">سمت</th>
                <th scope="col">قسمت</th>
                <th scope="col">بخش</th>
                <th scope="col">حذف کاربر</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(person,index) in filteredRows" :key="person">
                <th scope="row">{{index+1}}</th>
                <td>{{person.name}}</td>
                <td>{{person.lastname}}</td>
                <td>{{person.code}}</td>
                <td>{{person.who}}</td>
                <td>{{person.div}}</td>
                <td>{{person.section}}</td>
                <td><button class="btn btn-primary" @click="openModal4(index)">تغییر رمز</button> </td>
                </tr>
            </tbody>
        </table>
    </div>
 
</main>
</template>

<script>
import Header from '../../components/Header.vue'
import ChangeModal from '../../components/ChangeModal.vue'
import axios from 'axios'
export default {
    name:'ChangePassword',
    components:{Header,ChangeModal},
    data() {
        return {
            content:'تغییر رمز ورود',
            filter:'',
            list:null,
            modalVisible4:false,
            modalData4:null
        }
    },
    created() {
        axios.get('../users.json')
        .then(({data})=>{
            this.list=data;
        })
    },
    computed: {
         filteredRows() {
            return this.list.filter(person => {
            const name = person.name.toString().toLowerCase();
            const lastname = person.lastname.toString().toLowerCase();
            const code = person.code.toString();
            const searchTerm = this.filter.toLowerCase();

            return name.includes(searchTerm) || 
            lastname.includes(searchTerm) ||
            code.includes(searchTerm);
            });
         }
    },
    methods: {
        openModal4(msg){
            this.modalData4 = msg;
            this.modalVisible4 = true;

        }
    },
    
      
}
</script>

<style lang="scss" scoped>
.list{
        max-height: 65vh;
        overflow-y: scroll;
        table{
            
            direction: rtl;
            background-color: rgb(255, 255, 255);
            border-radius: 2px;
            text-align: center;
    
                thead{
                    background-color: #082C68;
                    color: rgb(255, 255, 255);
                    
        
                }
                    
        }
    }
</style>